import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
// ????
NProgress.inc(0.2);
NProgress.configure({ easing: 'ease', speed: 500, showSpinner: false })
Vue.use(Router)
//NavigationDuplicated {_name: "NavigationDuplicated"}
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const router =  new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: (resolve) => require(['@/pages/login/login'], resolve)
    },
    {
      path: '/achievementAudit',
      name: 'achievementAudit',
      component: (resolve) => require(['@/pages/achievementAudit'], resolve),
      meta:{
        activeIndex:'1-1',
      }
    },
    {
      path: '/achievementAudit/details',
      name: 'details',
      component: (resolve) => require(['@/pages/achievementAudit/details'], resolve),
      meta:{
        activeIndex:'1-1',
      }
    },
    {
      path: '/showManage',
      name: 'showManage',
      component: (resolve) => require(['@/pages/showManage'], resolve),
      meta:{
        activeIndex:'1-2',
      }
    },
    {
      path: '/informationManage',
      name: 'informationManage',
      component: (resolve) => require(['@/pages/informationManage'], resolve),
      meta:{
        activeIndex:'1-3',
      }
    },
    {
      path: '/informationManage/writeArticle',
      name: 'writeArticle',
      component: (resolve) => require(['@/pages/informationManage/writeArticle'], resolve),
      meta:{
        activeIndex:'1-3',
      }
    },
    {
      path: '/articleDetail',
      name: 'articleDetail',
      component: (resolve) => require(['@/pages/informationManage/articleDetail'], resolve),
      meta:{
        activeIndex:'1-3',
      }
    },
    {
      path: '/activeDirectory',
      name: 'activeDirectory',
      component: (resolve) => require(['@/pages/achievementDiscuss/activeDirectory'], resolve),
      meta:{
        activeIndex:'2-2',
      }
    },
    {
      path: '/activeAdd',
      name: 'activeAdd',
      component: (resolve) => require(['@/pages/achievementDiscuss/activeAdd'], resolve),
      meta:{
        activeIndex:'2-2',
      }
    },
    {
      path: '/matchAllocation',
      name: 'matchAllocation',
      component: (resolve) => require(['@/pages/achievementDiscuss/matchAllocation'], resolve),
      meta:{
        activeIndex:'2-3',
      }
    },
    {
      path: '/auditConclusion',
      name: 'auditConclusion',
      component: (resolve) => require(['@/pages/achievementDiscuss/auditConclusion'], resolve),
      meta:{
        activeIndex:'2-1',
      }
    },
    {
      path: '/activeDetails',
      name: 'activeDetails',
      component: (resolve) => require(['@/pages/achievementDiscuss/activeDetails'], resolve),
      meta:{
        activeIndex:'2-2',
      }
    },
    {
      path: '/achievementDetail',
      name: 'achievementDetail',
      component: (resolve) => require(['@/pages/achievementDiscuss/achievementDetail'], resolve),
      meta:{
        activeIndex:'2-1',
      }
    },
  ]
})
router.beforeEach((to,from,next) => {
  NProgress.start() 
  next()
})

router.afterEach(() => {
  NProgress.done()
})
export default router;


