// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
import App from './App'
import router from './router'
import store from './store/index.js'
//导入公共样式
import './assets/css/config.css'
import './assets/css/base.css'
//挂在url
import {api, formatDate,getdate,net,districtList, fastDfsUrl, loginUrl} from './assets/js/common';
Vue.prototype.net = net;
Vue.prototype.api = api;
Vue.prototype.districtList = districtList;
Vue.prototype.url = api.url;
Vue.prototype.fastDfsUrl = api.fastDfsUrl;
Vue.prototype.loginUrl = api.loginUrl;
Vue.prototype.formatDate = formatDate;
Vue.prototype.getdate = getdate;

//http请求
import axios from 'axios';

Vue.prototype.$http = axios;
import {post,fetch} from './http/http';
Vue.prototype.$post=post;
Vue.prototype.$fetch=fetch;
Vue.use(ElementUI);

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

Vue.config.productionTip = false
import vfilters from './assets/js/filter.js';//引入全局过滤器
for(let key in vfilters){
  Vue.filter(key,vfilters[key]);
}
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



