<template>
  <div id="app">
    <div v-if="show">
      <manageHeader></manageHeader>
      <tab></tab>
    </div> 
    <router-view/>
  </div>
</template>

<script>
  import manageHeader from "./components/header";
  import tab from "./components/tab";
  export default {
    data() {
      return {
        show: false
      }
    },
    created() {
      this.updateToken()
    },
    methods: {
      //验证token是否过期
      updateToken() {
        let accessToken = localStorage.getItem("accessToken")
        if (accessToken) {
          let url =this.api.configIp + this.net.updateToken;
          let params = {
            accessToken: accessToken
          }
          this.$fetch(url,params).then((res)=>{
            if(res.success){
              this.$store.commit("saveUserInfo",JSON.parse(res.data))
            }
          }).catch((err)=>{
            console.log('err:',err)
          })
        }
      },
    },
    watch: {
      $route (to, from) {
        this.show = to.name == 'login' ? false : true;
        let isLogin = localStorage.getItem('userName') || sessionStorage.getItem('userName');
        if (!isLogin) { // 判断是否已经登录

          this.$router.push({
            path: "/",
          })
          
          if(from.name){
            this.$message({
              message: '请重新登录',
              type: 'warning'
            });
          }
        }
      }
    },
    components:{
      manageHeader,
      tab
    }
  }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size:14px;
}
#nprogress .bar {
  z-index: 9999999;
  background: #488AFF !important;
}
html,body{
  margin : 0;
  padding: 0;
}
.el-submenu__title{
  text-align: left;
  padding-left:70px!important;
}
.inputSearch input{
  width:200px;
  padding-left:30px;
}
.clearfix:after{
  display:block;
  clear:both;
  visibility:hidden;
  height:0;
  content:" ";
  font-size:0;
}
.clearfix{
  *zoom:1;
}
.result{
  text-align:left;
}
.result .el-radio{
  display:block;
  margin-bottom: 20px;
  text-align:left;
}
.result .el-radio__inner{
  border-radius:0;
}
.el-radio__inner::after{
  border-radius:0;
  width:0px;
  height:0px;
  content:'√';
  color:white;
  left:-1px;
  top:0px;
  font-size:4px;
  font-family:cursive;
  transform: rotate3d(1, 0, 1, 30deg) translate(-50%,-50%) scale(1);
}
.editor .w-e-text-container{
  padding-top: 10px;
  height: 500px!important;
}
.el-message{
  z-index:10000000!important;
}
.w-e-toolbar{
  position: relative;
}

.result .el-radio__label{
  display:inline-block;
  width:220px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.result .el-radio__input{
  top:-2px;
}
.achievementContent .el-dialog__header{
  background: rgb(233,233,233);
}
.achievementContent .el-dialog{
  border-radius:5px;
  overflow:hidden;
  text-align:left;
}
.el-dialog .el-dialog__body{
  padding: 10px 20px;
}
.requirement .el-cascader{
  width: 180px;
  margin-left: 10px
}
.requirement .el-cascader .el-input__inner::-webkit-input-placeholder {
  color: #606266;
}
.blogText img{
  /* width:100%; */
  margin-top: 20px;
}
.objectRightContent .content img{
  width:100%
}
.noData{
  text-align: center !important;
}
</style>



