<template>
  <div class="header">
    <img src="../assets/images/logo.png">
    <span>微科技后台管理</span>
    <div class="info">
      <span class="userName">{{userInfo.userName}}</span>
      <span class="logout" @click="logout">注销</span>
    </div>
  </div>
</template>

<style scoped lang="less">
  .header{
    background: rgb(13,13,35);
    text-align: left;
    padding: 10px 40px;
    position: fixed;
    z-index: 300000;
    left: 0;
    top: 0;
    width: 100%;
    height: 40px;
    img{
      vertical-align: middle;
      width: 70px;
      height: 40px;
    }
    span{
      color: #c8c8c8;
      margin-left: 30px;
      position: relative;
      top: 4px;
      font-size: 16px;
    }
    .info{
      float: right;
      margin-right: 100px;
      margin-top: 10px;
      color: white;
      cursor: pointer;
    }
  }
</style>

<script>
import {mapState} from 'vuex';
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
    }
  },
  computed: {
    ...mapState({
      userInfo:(state)=>state.mutations.userInfo
    })
  },
  methods: {
    logout() {
      this.$router.push({
        path:"/"
      })
    }
  }
}
</script>






