import Vue from 'vue';
import {
  api
} from './common.js';
const vfilter = {
  replaceImgUrl : function(item){
    item = encodeURI(item.toString());
    if (item.indexOf('ga')) {
      return item.replace(/^http:\/\/.+ga\//, api.hostIp + "/")
    }  
  },
  timeStampFormat:function(data){
    var result;
    // tameStamp 格式化
    let str = data;
    //将字符串转换成时间格式
    let timePublish = new Date(str);
    let timeNow = new Date();
    let minute = 1000 * 60;
    let hour = minute * 60;
    let day = hour * 24;
    let month = day * 30;
    let diffValue = timeNow - timePublish;
    let diffMonth = diffValue / month;
    let diffWeek = diffValue / (7 * day);
    let diffDay = diffValue / day;
    let diffHour = diffValue / hour;
    let diffMinute = diffValue / minute;

    if (diffValue < 0) {
      result = "刚刚发表";
    } else if (diffMonth > 3) {
      result = timePublish.getFullYear() + "-";
      result += timePublish.getMonth() + "-";
      result += timePublish.getDate();
    } else if (diffMonth > 1) {
      result = parseInt(diffMonth) + "月前";
    } else if (diffWeek > 1) {
      result = parseInt(diffWeek) + "周前";
    } else if (diffDay > 1) {
      result = parseInt(diffDay) + "天前";
    } else if (diffHour > 1) {
      result = parseInt(diffHour) + "小时前";
    } else if (diffMinute > 1) {
      result = parseInt(diffMinute) + "分钟前";
    } else {
      result = "刚刚发表";
    }
      return result;
  },
  compilerLabel:function(s){
    var s= s.replace(/&nbsp;/gi,'');
    var d= s.replace(/<\/?.+?>/g,"");
    var e = d.replace(/ /g,"");
    var g = e.replace(/\[.*?\]/g,'')
    return g;
  },
  timeVersion:function(item){
    var Time = new Date(item);
    Time = Time.toLocaleDateString().replace(/\//g, "-");
    return Time;
  },
  getdate:function(item) {
    var now = new Date(item),
    y = now.getFullYear(),
    m = now.getMonth() + 1,
    d = now.getDate();
    //return y + "." + (m < 10 ? "0" + m : m) + "." + (d < 10 ? "0" + d : d)
    return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 5);
    //return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
  },
  getDate:function(item) {
    var now = new Date(item),
    y = now.getFullYear(),
    m = now.getMonth() + 1,
    d = now.getDate();
    return y + "." + (m < 10 ? "0" + m : m) + "." + (d < 10 ? "0" + d : d)
  },
  delHtmlTag:function(str){
   return str.replace(/<[^>]+>/g,"").replace(/&nbsp;/g,"");
  }
}
export default vfilter;




