  
  import * as types from './types';

  const state = {
    addMask : Object,
    twoCategory: Object,
    refreshDataLocal: Object,
    achievementMsg: Object,
    userInfo:{},
  };

  import getters from  './getters' 

  const mutations = {
    [types.ADDMASK](state, res) {
      state.addMask = res;
    },
    [types.TWOCATEGORY](state, res){
      state.twoCategory = res;
    },
    //指的是刷新成果首页位置1或2或三或者成果类别， 1指的是首页位置1 和成果类别1  2 指的是首页位置2   3指的是首页位置3
    [types.REFRESHDATALOCAL](state, res){
      state.refreshDataLocal = res;
    },
    [types.ACHIEVEMENTMSG](state, res){
      state.achievementMsg = res;
    },
    [types.saveUserInfo](state, infos) {
      console.log('信息:',infos)
      state.userInfo = infos
    },
  };
  
  export default {
    state,
    mutations,
    getters
  };


