<template>
  <div>
    <div class="tab">
    	<!-- <div class="menu">
    		<div class="subMenu" @click="toggle" :class="{imgActive : imgActive}">
    			<img src="../assets/images/tabIcon.png" class="tabIcon">
    			<span>微科技</span>
    			<i class="el-icon-arrow-up"></i>
    		</div>
        <transition name="fade">
      		<div class="item" v-show= "fade">
      			<ul>
      				<li 
                v-for="(item, index) in options"
                @click="changeSelect(index)"
                :class="{active: cur === index}"
                :key="index">
                {{item.tabName}}
              </li>
      			</ul>
      		</div>  
        </transition>     
    	</div> -->
      <el-menu
        :default-active="$route.meta.activeIndex" class="el-menu-vertical-demo"
        active-text-color="#5a5a5a">
        <el-submenu index="1">
          <template slot="title">
            <img src="../assets/images/tabIcon.png" class="tabIcon">
            <span>微科技</span>
          </template>
          <el-menu-item-group>
              <el-menu-item :index="item.index" v-for="(item,i) in options" :key="i"  @click="changeSelect(item.index)">{{item.tabName}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="2">
          <template slot="title">
            <img src="../assets/images/flag.png" class="flagIcon">
            <span>微科技成果评比</span>
          </template>
          <el-menu-item-group>
            <el-menu-item :index="item.index" v-for="(item,i) in options2" :key="i"  @click="changeSelect(item.index)">{{item.tabName}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </div>
  </div>
</template>
<style lang="less" scoped>
  .tab{
  	width: 260px;
  	position: fixed;
    font-size: 14px;
  	left: 0;
  	top: 60px;
    height: 100%;
  	background: rgb(236,236,236);
    box-shadow: 0 0 2px #eee;
  	.subMenu{
  		text-align: left;
  		padding: 20px 70px;
  		.tabIcon{
	  	  width: 14px;
	  	  margin-right: 10px;
	  	}
	  	i{
				position: absolute;
				right: 20px;
			}
  	}
    .imgActive{
      .el-icon-arrow-up{
        transform: rotate(180deg);
        transition: all 0.5s ease-out;
      }
    }
    .fade-enter-active, .fade-leave-active {
      transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
  	.item{  
  		ul{
  			margin:0;
  			padding:0;
  			li{
  				list-style-type: none;
  			  height: 50px;
  			  line-height: 50px;
          cursor: pointer;
  			}
  			.active{
			  	background: white;
			  }
  		}
    }
    .el-menu{
      background-color: #ececec;
      .el-submenu{
        .el-submenu__title{
          .tabIcon{
             width: 14px;
             height: 14px;
	  	       margin-right: 10px;
          }
          .flagIcon{
             width: 11px;
             height: 16px;
	  	       margin-right: 10px;
          }
        }
      }
    }
  }
</style>
<script>
  import {mapState} from 'vuex';
  export default {
    data() {
      return {
        options: [
          {
            tabName: "成果审核",
            index: '1-1'
          },
          {
            tabName: "展示管理",
            index: '1-2'
          },
          {
            tabName: "资讯管理",
            index: '1-3'
          }
        ],
        options2: [
          {
            tabName: "参赛成果审核",
            index: '2-1'
          },
          {
            tabName: "活动列表",
            index: '2-2'
          },
          {
            tabName: "参赛码设置",
            index: '2-3'
          }
        ],
        fade: true,
        imgActive: false
      }
    },
    computed: {
      ...mapState({
        userInfo:(state)=>state.mutations.userInfo
      })
    },
    created() {
      this.judgeIsAdmin()
      console.log('xinxi:',this.userInfo)
    },
    methods: {
      changeSelect(i) {
        switch(i){
          case '1-1' : 
            this.$router.push({
              path : '/achievementAudit'
            })
          break;
          case '1-2' : 
            this.$router.push({
              path : '/showManage'
            })
          break;
          case '1-3' : 
            this.$router.push({
              path : '/informationManage'
            })
          break;
          case '2-1' : 
            this.$router.push({
              path : '/auditConclusion'
            })
          break;
          case '2-2' : 
            this.$router.push({
              path : '/activeDirectory'
            })
          break;
          case '2-3' : 
            this.$router.push({
              path : '/matchAllocation'
            })
          break;
        }
      },
      toggle() {
        this.fade = !this.fade;
        this.imgActive = !this.imgActive;
      },
      //判断是否为超级管理员来显示不同type
      judgeIsAdmin(){
        let options = [
          {
            tabName: "成果审核",
            index: '1-1'
          }
        ];
        let options2= [
          {
            tabName: "参赛成果审核",
            index: '2-1'
          },
          {
            tabName: "参赛码设置",
            index: '2-3'
          }
        ];
        let isAdminType = this.userInfo.type;
        if(isAdminType==2){
          this.options = options;
          this.options2 = options2
        }
      }
    }
  }
</script>


